<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">模型训练</div>
                                </div>
                                <div class="subbtn">
                                    <div v-for="(item,index) in tabList" :class="activeClass==tabList[index].sceneName?'active':''" @click="changeBtn(tabList[index].sceneName)">
                                        <el-badge :value="tabList[index].institution" class="item">
                                            <p>{{tabList[index].sceneName}}</p>
                                        </el-badge>
                                    </div>
                                </div>
                                <div class="screen-block">
                                    <el-button v-if="tabList.length>0" class="yellow-btn-style" icon="el-icon-s-promotion" @click="handleAdd">发起训练</el-button>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="account-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="taskName" label="任务名称"></el-table-column>
                                            <el-table-column prop="modelName" label="模型"></el-table-column>
                                            <el-table-column prop="parameters" label="参数">
                                                <template slot-scope="scope">
                                                    <div>
                                                        <p v-for="(item,index) in scope.row.parameters.split(',')">{{item.split('{')[1].split(':')[0]+`：`+item.split(':')[1].split('}')[0]}}</p>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="datasetName" label="数据集"></el-table-column>
                                            <el-table-column prop="auc" label="AUC">
                                                <template slot-scope="scope">
                                                    <p v-if="scope.row.status=='等待运行'||scope.row.status=='训练失败'">-</p>
                                                    <el-button style="cursor: auto;background: #26374c;" type="info" size="small" :loading="true" v-if="scope.row.status=='训练中'"></el-button>
                                                    <p v-if="scope.row.status=='训练完成'">{{scope.row.auc}}</p>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="ks" label="KS">
                                                <template slot-scope="scope">
                                                    <p v-if="scope.row.status=='等待运行'||scope.row.status=='训练失败'">-</p>
                                                    <el-button style="cursor: auto;background: #26374c;" type="info" size="small" :loading="true" v-if="scope.row.status=='训练中'"></el-button>
                                                    <p v-if="scope.row.status=='训练完成'">{{scope.row.ks}}</p>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="status" label="状态"></el-table-column>
                                            <el-table-column label="操作" width="260px">
                                                <template slot-scope="scope">
                                                    <div class="cell">
                                                        <div v-if="scope.row.status=='训练中'" @click="trainProgress(scope.$index, scope.row)"  class="buttonDiv">
                                                            <img src="static/images/inspect.png" alt="" width="25" height="25">
                                                            <p>训练过程</p>
                                                        </div>
                                                        <div v-if="scope.row.status=='训练完成'" @click="trainResult(scope.$index, scope.row)"  class="buttonDiv">
                                                            <img src="static/images/anysis.png" alt="" width="25" height="25">
                                                            <p>训练效果</p>
                                                        </div>
                                                        <div v-if="scope.row.status=='训练完成'||scope.row.status=='训练失败'" @click="trainAgain(scope.$index, scope.row)"  class="buttonDiv">
                                                            <img src="static/images/reset.png" alt="" width="25" height="25">
                                                            <p>重新训练</p>
                                                        </div>
                                                        <div @click="handleDelete(scope.$index, scope.row)"  class="buttonDiv">
                                                            <img src="static/images/del.png" alt="" width="25" height="25">
                                                            <p>删除</p>
                                                        </div>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                        <el-pagination
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[5,10,20,50]"
                                            :page-size="pagesize"
                                            layout="total, sizes, prev, pager, next, jumper"
                                            :total="tableData.length"
                                            style="margin-top:15px">
                                        </el-pagination>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
                <el-dialog title="发起训练 " :visible.sync="dialogVisible" width="500px" :lock-scroll="false">
                    <el-form ref="form" :rules="rulesAdd" :model="form" label-width="100px">
                        <el-form-item label="任务名称" prop="taskName">
                            <el-input v-model="form.taskName"></el-input>
                        </el-form-item>
                        <el-form-item label="数据集名称" prop="dataSelect">
                            <el-select class="jspSelect" v-model="form.dataSelect" value-key="id" placeholder="请选择" @change="selectChange">
                                <el-option
                                  v-for="item in datasetList"
                                  :key="item.datasetName"
                                  :label="item.datasetName"
                                  :value="item.datasetName">
                                  <span style="float: left">{{ item.datasetName }}</span>
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="模型" prop="modelSelect">
                            <el-select class="jspSelect" v-model="form.modelSelect" placeholder="请选择">
                                <el-option
                                  v-for="item in modelList"
                                  :key="item"
                                  :label="`模型名称：${item}`"
                                  :value="item"
                                  @click.native="option_click_handler(item)">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <template>
                            <el-form-item class="paramsOne" :data-name='dataArr[index].name' :key="dataArr[index].name" :label="dataArr[index].name" v-for="(item,index) in dataArr" :data-checkvalue="dataArr[index].value">
                                <el-input v-model="dataArr[index].value"></el-input>
                            </el-form-item>
                        </template>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="addTasks('form')">开始训练</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisible = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="重新训练 " :visible.sync="dialogVisibleAgain" width="500px" :lock-scroll="false">
                    <el-form ref="formAgain" :model="formAgain" label-width="100px">
                        <el-form-item label="任务名称">
                            <el-input v-model="formAgain.taskName" disabled></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="againTasks">开始训练</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleAgain = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="训练效果" :visible.sync="dialogVisibleResult" width="700px" :lock-scroll="false" id="contrast">
                    <div class="valuesArr">
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">正确率：预测正确的结果所占总数的比例。<br/>越大模型效果越好。<br/>正确率高并不能说明分类器效果好。比如识别<br/>一个人是否有艾滋病，就算全部预测为负样本，<br/>那正确率也有99%以上（因为每万人才有6个艾<br/>滋病人），但是这种高正确率的分类器其实并<br/>没有任何作用。</div>
                            <p :class="{active:shownum==1}" @click="addactive(1,'accuracy')">Accuracy</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">想要得到很高的召回率，就要牺牲掉一些精准<br/>率。F1分数同时考虑精确率和召回率，让两者<br/>同时达到最高，取得平衡。<br/>越大模型效果越好。</div>
                            <p :class="{active:shownum==2}" @click="addactive(2,'f1')">F1</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">精确率（查准率）：所有预测出来的正例中有<br/>多少是正确的比例。<br/>越大模型效果越好。</div>
                            <p :class="{active:shownum==3}" @click="addactive(3,'precision')">Precision</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">召回率（查全率）：正例被分类器正确探测<br/>出的比率，描述的是所有正例我能发现多少。<br/>越大模型效果越好。</div>
                            <p :class="{active:shownum==4}" @click="addactive(4,'recall')">Recall</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">F1认为精确率和召回率的权重是一样的, 但<br/>有些场景下, 我们可能认为精确率会更加重<br/>要, 调整参数获得Fbeta。<br/>越大模型效果越好。</div>
                            <p :class="{active:shownum==5}" @click="addactive(5,'fbeta')">FBeta</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" placement="top">
                            <div slot="content">对数损失，也称为逻辑回归损失或交叉熵损<br/>失，描述全局样本预测结果和真实结果的差<br/>值是根据概率估计定义的。<br/>它通常用于（多项式）逻辑回归和神经网络，且<br/>可用于评估分类器的概率输出（predict_proba）<br/>而不是其离散预测。<br/>越小模型效果越好。</div>
                            <p :class="{active:shownum==6}" @click="addactive(6,'logloss')">Log Loss</p>
                        </el-tooltip>
                    </div>
                    <div id="selfChart1" class="allcharts"></div>
                </el-dialog>
                <el-dialog title="" :visible.sync="dialogVisibleProgress" width="700px" :lock-scroll="false">
                    <p style="text-align: center;width: 100%;font-size: 18px;font-weight: bold;margin-top: -40px;margin-bottom: 10px;">{{statusCN}}</p>
                    <div id="selfChartProgress"></div>
                </el-dialog>
                <el-dialog title="" :visible.sync="dialogVisibleDel" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <img src="static/images/del_tip.png" class="del-tip-style" alt="">
                    <p class="del-tip-p">确定删除？</p>
                    <el-form ref="formDel" :model="formDel" label-width="80px">
                        <el-form-item label="ID" style="display:none">
                            <el-input v-model="formDel.taskName"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="deleteThis">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleDel = false">取 消</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/modelTrain.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
var echarts=require('echarts/lib/echarts')
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/chart/pie");
require("echarts/lib/chart/scatter");
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
require('echarts/lib/component/legend');
export default {
    name: "modelTrain",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            tabList:[],
            activeClass:'',
            tableData: [],
            pagesize:10,
            currentPage:1,
            // 发起训练
            dialogVisible:false,
            rulesAdd:{
                taskName:[{ required: true, message: '请输入任务名称', trigger: 'blur' }],
                dataSelect: [{ required: true, message: '请选择数据集', trigger: 'change' }],
                modelSelect:[{ required: true, message: '请选择模型', trigger: 'change' }],
            },
            form:{},
            datasetList:[],
            modelList:[],
            dataArr:[],

            // 重新训练
            dialogVisibleAgain:false,
            formAgain:{},

            // 训练效果
            dialogVisibleResult:false,
            shownum:1,
            taskName:'',

            // 训练过程
            dialogVisibleProgress:false,
            statusCN:'',

            // 删除弹窗
            dialogVisibleDel:false,
            formDel:{},
        }
    },
    mounted(){
        this.findDimensions()
        this.getSceneList()
        this.getModelList()
    },
    methods: {
        // 训练任务列表
        getTaskList:function(){
            var that = this;
            that.axios.post('/taskReq',that.qs.stringify({
                "scene": that.activeClass,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.tableData=response.data.tasks
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        // 数据集列表
        getDataList:function(){
            var that = this;
            that.axios.post('/datasetReq',that.qs.stringify({
                "scene": that.activeClass,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.datasetList=response.data.datasets
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        // 模型列表
        getModelList:function(businessLine){
            var that = this;
            that.axios.post('/modelReq',that.qs.stringify({
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.modelList=[]
                    var data=response.data.models
                    $.each(data,function(index, el) {
                        that.modelList.push(el)
                    });
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        // 选择模型后列出对象参数
        option_click_handler(data) {
            var that = this;
            that.axios.post('/modelInfoReq',that.qs.stringify({
                "modelName":data,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    var data=response.data.defaultParams;
                    var len=data.length
                    that.dataArr=[]
                    for(var i=0;i<len;i++){
                        that.dataArr.push({name:data[i].split('（')[0],value:data[i].split('（默认')[1].split('）')[0]})
                    }
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },


        // 发起训练
        handleAdd:function(){
            var that=this;
            that.dialogVisible=true;
        },
        // 开始训练
        addTasks:function(form){
            var that=this;
            that.$refs[form].validate((valid) => {
                if (valid) {
                    var emptyCount=0;
                    var paramters="["
                    $.each(that.dataArr,function(index, el) {
                        paramters+='{'+el.name+':'+el.value+'},';
                        if(el.value==""){
                            emptyCount++
                        }
                    });
                    if(emptyCount>0){
                        that.$message({
                            message: '请完善模型参数',
                            type: 'error'
                        });
                    }
                    else {
                        that.dialogVisible=false;
                        that.axios.post('/taskLaunch',that.qs.stringify({
                            'scene':that.activeClass,
                            'taskName':that.form.taskName,
                            'datasetName':that.form.dataSelect,
                            'modelName':that.form.modelSelect,
                            'parameters':paramters.substring(0,paramters.length-1)+']',
                            "operator":that.username,
                            "token":that.token
                        }),
                        {
                            headers:{
                                'Content-Type':'application/x-www-form-urlencoded',
                            }
                        },
                        {
                            emulateJSON:true
                        })
                        .then(function(response){
                            if(response.data.message.state){
                                that.$message({
                                    message: '发起训练成功',
                                    type: 'success'
                                });
                                that.currentPage=1;
                                that.getTaskList()
                            }
                            else {
                                that.$message.error(response.data.message);
                            }
                        },function(error){
                            that.$message.error(error.response.data.message);
                            if(error.response.status==401){
                                that.$router.push('/');
                                Cookies.set("fedname",1,{expires:-1});
                                Cookies.set("fedrole",1,{expires:-1});
                            }
                        })
                    }
                }
                else {
                    return false;
                }
            });
        },


        // 重新训练
        trainAgain:function(index, rowData){
            var that=this;
            that.dialogVisibleAgain=true;
            that.formAgain = Object.assign({}, rowData);
        },
        againTasks:function(){
            var that=this;
            that.dialogVisibleAgain=false;
            that.axios.post('/taskRestart',that.qs.stringify({
                'scene':that.activeClass,
                'taskName':that.formAgain.taskName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '重新训练成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getTaskList()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 训练效果
        trainResult:function(index, rowData){
            var that=this;
            that.taskName=rowData.taskName
            that.dialogVisibleResult=true;
            that.getAnalysis('accuracy')
        },
        addactive:function(activeid,str){
            var that=this;
            that.shownum=activeid
            that.getAnalysis(str)
        },
        getAnalysis:function(str){
            var that = this;
            that.axios.post('/taskResReq',that.qs.stringify({
                'scene':that.activeClass,
                'taskName':that.taskName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                var data=response.data.result;
                console.log(str)
                if(str=='accuracy'){
                    var resultData=data.accuracy?(data.accuracy*1).toFixed(2):'0'
                }
                else if(str=='f1'){
                    var resultData=data.F1?(data.F1*1).toFixed(2):'0'
                }
                else if(str=='precision'){
                    var resultData=data.precision?(data.precision*1).toFixed(2):'0'
                }
                else if(str=='recall'){
                    var resultData=data.recall?(data.recall*1).toFixed(2):'0'
                }
                else if(str=='fbeta'){
                    var resultData=data.FBeta?(data.FBeta*1).toFixed(2):'0'
                }
                else if(str=='logloss'){
                    var resultData=data.logLoss?(data.logLoss*1).toFixed(2):'0'
                }
                var myChart = echarts.init(document.getElementById('selfChart1'))
                myChart.clear()
                myChart.setOption({
                    grid: {
                        left: '30',
                        right: '50',
                        bottom: '3%',
                        top:'40',
                        containLabel: true
                    },
                    legend:{
                        show:false
                    },
                    xAxis: [{
                        type: 'category',
                        name:'',
                        nameTextStyle:{
                            color:'#666'
                        },
                        data: [str],
                        axisTick: {
                            show:false
                        },
                        axisLine:{
                            show:false
                        },
                        axisLabel:{
                            show:true,
                            textStyle:{
                                fontSize:12,
                                color:'#666'
                            },
                        }
                    }],
                    yAxis: [{
                        type: 'value',
                        name:'',
                        nameTextStyle:{
                            color:'#666'
                        },
                        axisLabel:{
                            show:true,
                            color:'#666'
                        },
                        axisLine:{
                            show:true,
                            lineStyle:{
                                color:'#ddd'
                            }
                        },
                        splitLine:{
                            show:true,
                            lineStyle:{
                                color:'#ddd'
                            }
                        },
                        axisTick:{
                            show:false,
                        },
                    }],
                    series: [{
                        name: '',
                        type: 'bar',
                        barWidth: '30%',
                        data: [resultData],
                        barMaxWidth:'40',
                        itemStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(
                                    0, 0, 0, 1,
                                    [
                                        {
                                            offset: 0,
                                            color: '#3781E8'
                                        },
                                        {
                                            offset: 1,
                                            color: '#2048f4'
                                        }
                                    ]
                                )
                            }
                        },
                        label:{
                            show:true,
                            position:'top',
                            color:'#666',
                            formatter:"{c}"
                        }
                    }]
                });
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },


        // 训练过程
        trainProgress:function(index, rowData){
            var that=this;
            that.dialogVisibleProgress=true;
            that.axios.post('/taskStateReq',that.qs.stringify({
                'scene':that.activeClass,
                'taskName':rowData.taskName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    var data=response.data.status
                    that.statusCN=data;
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
            that.axios.post('/taskInfoReq',that.qs.stringify({
                'scene':that.activeClass,
                'taskName':rowData.taskName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    var data=response.data.roundInfo
                    var myChart = echarts.init(document.getElementById('selfChartProgress'))
                    var roundInfoArr=[],aucArr=[],ksArr=[];
                    $.each(data,function(index, el) {
                        roundInfoArr.push(el.round)
                        aucArr.push(el.auc.toFixed(2))
                        ksArr.push(el.ks.toFixed(2))
                    });
                    myChart.clear()
                    myChart.setOption({
                        grid: {
                            left: '30',
                            right: '50',
                            bottom: '40',
                            top:'40',
                            containLabel: true
                        },
                        legend:{
                            show:true,
                            top:'bottom',
                            left:'center',
                        },
                        xAxis: [{
                            type: 'category',
                            name:'轮次',
                            nameTextStyle:{
                                color:'#666'
                            },
                            data: roundInfoArr,
                            axisTick: {
                                show:false
                            },
                            axisLine:{
                                show:false
                            },
                            axisLabel:{
                                show:true,
                                textStyle:{
                                    fontSize:12,
                                    color:'#666'
                                },
                                formatter:function(params){
                                    var newParamsName = "";
                                    var paramsNameNumber = params.length;
                                    var provideNumber = 8;  //一行显示几个字
                                    var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                                    if (paramsNameNumber > provideNumber) {
                                        for (var p = 0; p < rowNumber; p++) {
                                            var tempStr = "";
                                            var start = p * provideNumber;
                                            var end = start + provideNumber;
                                            if (p == rowNumber - 1) {
                                                tempStr = params.substring(start, paramsNameNumber);
                                            } else {
                                                tempStr = params.substring(start, end) + "\n";
                                            }
                                            newParamsName += tempStr;
                                        }

                                    } else {
                                        newParamsName = params;
                                    }
                                    return newParamsName
                                }
                            }
                        }],
                        yAxis: [{
                            type: 'value',
                            name:'AUC/KS',
                            nameTextStyle:{
                                color:'#666'
                            },
                            axisLabel:{
                                show:true,
                                color:'#666'
                            },
                            axisLine:{
                                show:true,
                                lineStyle:{
                                    color:'#ddd'
                                }
                            },
                            splitLine:{
                                show:true,
                                lineStyle:{
                                    color:'#ddd'
                                }
                            },
                            axisTick:{
                                show:false,
                            },
                        }],
                        series: [{
                            name: 'AUC',
                            type: 'line',
                            data: aucArr,
                            itemStyle:{
                                color:'#2048f4'
                            },
                            symbol:'circle',
                            symbolSize:6,
                            label:{
                                show:true,
                                position:'top',
                                color:'#666',
                                formatter:"{c}"
                            }
                        },{
                            name: 'KS',
                            type: 'line',
                            data: ksArr,
                            itemStyle:{
                                color:'#f2572c'
                            },
                            symbol:'circle',
                            symbolSize:6,
                            label:{
                                show:true,
                                position:'top',
                                color:'#666',
                                formatter:"{c}"
                            }
                        }]
                    });
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 删除
        handleDelete(index, rowData) {
            this.dialogVisibleDel = true;
            this.formDel= Object.assign({}, rowData);
        },
        deleteThis:function(){
            var that=this;
            that.dialogVisibleDel=false;
            that.axios.post('/taskDelete',that.qs.stringify({
                "scene":that.activeClass,
                "taskName":that.formDel.taskName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '删除训练任务成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getTaskList()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        // 获取场景列表
        getSceneList:function(){
            var that = this;
            that.axios.post('/sceneReq',that.qs.stringify({
                "specific":false,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    if(response.data.scenes.length){
                        that.tabList=response.data.scenes
                        that.activeClass=response.data.scenes[0].sceneName;
                        that.getTaskList()
                        that.getDataList()
                    }
                    else {
                        that.tabList=[];
                        that.activeClass="";
                    }
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        changeBtn:function(id){
            var that=this;
            that.activeClass=id
            that.getTaskList()
            that.getDataList()
            that.form.dataSelect=''
        },

        handleSizeChange: function (size) {
            this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".account-list").attr("style","min-height:"+(this.winHeight-420)+"px;")
        },
    }
};
</script>



